"use client";

import React, { useEffect } from "react";
import { useRouter } from "next/navigation";
import { useAuth } from "@/context/AuthContext";
import { useQuery } from "@tanstack/react-query";
import CreateCVForm from "@/components/CreateCVForm";
import EditCVForm from "@/components/EditCVForm";

const backendUrl = process.env.NEXT_PUBLIC_BACKEND_URL || 'http://localhost:8000';

// This function checks for user profile existence
const fetchUserProfileExistence = async (accessToken: string) => {
  const response = await fetch(`${backendUrl}/api/v1/users/me/cv`, {
    method: 'GET',
    headers: {
      'Authorization': `Bearer ${accessToken}`,
    },
  });

  if (response.status === 404) {
    return null; // Profile does not exist
  }

  if (!response.ok) {
    const errorData = await response.json();
    throw new Error(errorData.detail || 'Failed to check profile existence.');
  }
  
  // Profile exists
  return true; // Return true if profile data was found
};

export default function CvCreationPage() {
  const { session, isLoading: isAuthLoading } = useAuth();
  const router = useRouter();

  // Redirect to login if not authenticated
  useEffect(() => {
    if (!isAuthLoading && !session) {
      router.push("/");
    }
  }, [session, isAuthLoading, router]);

  // Query to check if user profile exists
  const { data: profileExists, isLoading: isProfileLoading, isError: isProfileError, error: profileError } = useQuery({
    queryKey: ['userProfileExistence', session?.access_token],
    queryFn: () => fetchUserProfileExistence(session!.access_token!),
    enabled: !!session?.access_token, // Only run if authenticated
  });

  if (isAuthLoading || isProfileLoading) {
    return (
      <div className="flex items-center justify-center min-h-screen">
        <p>Loading...</p>
      </div>
    );
  }

  if (isProfileError) {
    return (
      <div className="flex items-center justify-center min-h-screen">
        <p className="text-red-600">Error: {profileError?.message || 'Failed to load profile status.'}</p>
      </div>
    );
  }

  return (
    <div className="flex items-center justify-center min-h-screen">
      <div className="w-full max-w-lg">
        {profileExists ? <EditCVForm /> : <CreateCVForm />}
      </div>
    </div>
  );
}