import React, { useState, useEffect } from 'react';
import { useForm } from 'react-hook-form';
import { useRouter } from 'next/navigation';
import { useAuth } from '@/context/AuthContext';
import { useUnsavedChanges } from '@/context/UnsavedChangesContext';
import { useQuery, useMutation, useQueryClient } from '@tanstack/react-query';
import { useNavigationBlocker } from '@/hooks/useNavigationBlocker';
import { parseISO, format } from 'date-fns'; // Import date-fns utilities

interface UserProfile {
  id: string;
  full_name: string;
  date_of_birth: string;
  gender: string;
  phone_number: string;
  address: string;
  cv_content: string;
  created_at: string;
  updated_at: string;
}

interface ProfileCVFormInputs {
  full_name: string;
  date_of_birth: string;
  gender: string;
  phone_number: string;
  address: string;
  cv_content: string;
}

const backendUrl = process.env.NEXT_PUBLIC_BACKEND_URL || 'http://localhost:8000';

const fetchUserProfileCvData = async (accessToken: string): Promise<UserProfile> => {
  const response = await fetch(`${backendUrl}/api/v1/users/me/cv`, {
    method: 'GET',
    headers: {
      'Authorization': `Bearer ${accessToken}`,
    },
  });

  // For EditCVForm, a 404 means no profile exists, which is an error in this context.
  // The parent component should ensure this component is only rendered if a profile exists.
  if (!response.ok) {
    const errorData = await response.json();
    throw new Error(errorData.detail || 'Failed to load profile and CV data.');
  }

  const userProfile = await response.json();
  console.log('DEBUG: EditCVForm - fetchUserProfileCvData returning:', userProfile); // Add this
  return userProfile;
};

const updateUserProfileCvData = async (accessToken: string, formData: ProfileCVFormInputs): Promise<UserProfile> => {
    const payload: Partial<ProfileCVFormInputs> = {};
    if (formData.full_name) payload.full_name = formData.full_name;
    if (formData.date_of_birth) payload.date_of_birth = formData.date_of_birth;
    if (formData.gender) payload.gender = formData.gender;
    if (formData.phone_number) payload.phone_number = formData.phone_number;
    if (formData.address) payload.address = formData.address;
    if (formData.cv_content) payload.cv_content = formData.cv_content;

    const response = await fetch(`${backendUrl}/api/v1/users/me/cv`, {
      method: 'PATCH',
      headers: {
        'Content-Type': 'application/json',
        'Authorization': `Bearer ${accessToken}`,
      },
      body: JSON.stringify(payload),
    });
  
    if (!response.ok) {
      const errorData = await response.json();
      throw new Error(errorData.detail || 'Failed to update profile and CV.');
    }
    
    const { data } = await response.json();
    return data;
};


const EditCVForm: React.FC = () => {
  const { register, handleSubmit, formState: { errors, isDirty }, reset, getValues } = useForm<ProfileCVFormInputs>();
  const router = useRouter();
  const { session } = useAuth();
  console.log('DEBUG: EditCVForm - Session:', session);
  console.log('DEBUG: EditCVForm - Access Token:', session?.access_token);
  const { setHasUnsavedChanges } = useUnsavedChanges();
  const [submissionError, setSubmissionError] = useState<string | null>(null);
  const queryClient = useQueryClient();
  const isInitialRender = React.useRef(true); // Ref to track initial render for form initialization

  const { mutateAsync, isPending: isMutating, isError: isMutationError, error: mutationError } = updateMutation;

  console.log('DEBUG: EditCVForm - Render: isDirty:', isDirty, 'isMutating:', isMutating, 'isUserCvLoading:', isUserCvLoading);

  const { data: userData, isLoading: isUserCvLoading, isError: isUserCvError, error: userCvError } = useQuery({
    queryKey: ['userProfileCv', session?.access_token],
    queryFn: () => fetchUserProfileCvData(session!.access_token!),
    enabled: !!session?.access_token,
    onSettled: (data, error) => {
      console.log('DEBUG: EditCVForm - useQuery onSettled - data:', data, 'error:', error);
    }
  });

  // ******* NEW useEffect for form population *******
  useEffect(() => {
    if (userData && isInitialRender.current) { // Only on initial load of userData
      console.log('DEBUG: EditCVForm - useEffect - Initializing form with userData:', userData);
      reset({
        full_name: userData.full_name || '',
        date_of_birth: userData.date_of_birth ? format(parseISO(userData.date_of_birth), 'yyyy-MM-dd') : '',
        gender: userData.gender || '',
        phone_number: userData.phone_number || '',
        address: userData.address || '',
        cv_content: userData.cv_content || '',
      });
      isInitialRender.current = false; // Mark as initialized
    }
  }, [userData, reset]); // Dependency array includes userData and reset

  console.log('DEBUG: EditCVForm - Query State: isLoading:', isUserCvLoading, 'isError:', isUserCvError, 'error:', userCvError);

  const updateMutation = useMutation({
    mutationFn: (formData: ProfileCVFormInputs) => updateUserProfileCvData(session!.access_token!, formData),
    onSuccess: (updatedData) => { // Use updatedData for clarity
      // Manually update the cache. This will NOT trigger reset(userData) in the useEffect
      // because isInitialRender.current will be false.
      queryClient.setQueryData(['userProfileCv', session?.access_token], (oldData: UserProfile | undefined) => {
        if (!oldData) return updatedData;
        return { ...oldData, ...updatedData };
      });
      // isDirty remains true if there are other unsaved changes.
      // setHasUnsavedChanges is updated by the useEffect below based on isDirty.
      console.log("DEBUG: Profile and CV auto-saved successfully! isDirty:", isDirty);
    },
    onError: (error) => {
      setSubmissionError(error.message || 'An unexpected error occurred during update.');
      console.log("DEBUG: Auto-save failed! Error:", error, "isDirty:", isDirty);
    },
  }); // <-- Added missing closing parenthesis and brace here


  // The useEffect for setHasUnsavedChanges should remain, as it tracks overall form dirty state
  useEffect(() => {
    console.log('DEBUG: EditCVForm - useEffect [isDirty]: isDirty changed to', isDirty);
    setHasUnsavedChanges(isDirty);
  }, [isDirty, setHasUnsavedChanges]);

  const { BlockerDialog } = useNavigationBlocker({
    when: isDirty,
    onConfirm: () => {
      // Discard changes
      console.log('DEBUG: BlockerDialog - Discarding changes. userData:', userData);
      reset(userData); // Reset to last fetched data
      setHasUnsavedChanges(false);
    },
    onSave: async () => {
      const formValues = getValues();
      try {
        const result = await mutateAsync(formValues);
        // On successful manual save, reset the form with the new data
        reset(result); // Reset form state and set isDirty to false
        setHasUnsavedChanges(false);
        queryClient.invalidateQueries({ queryKey: ['userProfileCv'] }); // Invalidate after manual save to ensure fresh data for next load
        console.log("DEBUG: Profile and CV manually saved successfully from BlockerDialog! isDirty:", isDirty);
        return true;
      } catch (e) {
        console.error('DEBUG: Manual save failed from BlockerDialog:', e);
        return false;
      }
    },
  });

  const onSubmit = async (data: ProfileCVFormInputs) => {
    if (!session?.access_token) {
      setSubmissionError("You must be logged in to update your profile.");
      return;
    }
    setSubmissionError(null);
    try {
      const result = await mutateAsync(data);
      reset(result); // Reset form state and set isDirty to false after manual submit
      setHasUnsavedChanges(false);
      queryClient.invalidateQueries({ queryKey: ['userProfileCv'] }); // Invalidate after manual save
      router.push('/dashboard');
      console.log("DEBUG: Profile and CV manually saved successfully from onSubmit! isDirty:", isDirty);
    } catch (e) {
      console.error('DEBUG: Mutation failed in onSubmit:', e);
    }
  };

  const handleBlur = () => {
    console.log('DEBUG: handleBlur initiated. isDirty:', isDirty, 'isMutating:', isMutating);
    // Only auto-save if the form is dirty and not already mutating
    if (!isUserCvLoading && isDirty && !isMutating) {
      const formValues = getValues();
      console.log('DEBUG: Auto-save triggered. formValues:', formValues);
      updateMutation.mutate(formValues);
    }
  };

  if (isUserCvLoading) {
    return <div className="p-4 text-center">Loading Profile and CV data...</div>;
  }

  if (isUserCvError) {
    return <div className="p-4 text-center text-red-600">Error loading Profile and CV data: {userCvError?.message}</div>;
  }

  return (
    <form onSubmit={handleSubmit(onSubmit)} className="space-y-4 p-4">
      <h2 className="text-2xl font-bold mb-4">Edit Your Profile and CV</h2>
      
      {(submissionError || isMutationError) && (
        <div className="bg-red-100 border border-red-400 text-red-700 px-4 py-3 rounded relative" role="alert">
          <strong className="font-bold">Error: </strong>
          <span className="block sm:inline">{submissionError || mutationError?.message}</span>
        </div>
      )}

      <div>
        <label htmlFor="full_name" className="block text-sm font-medium text-gray-700">Full Name</label>
        <input
          id="full_name"
          type="text"
          {...register('full_name', { required: 'Full Name is required' })}
          className="mt-1 block w-full rounded-md border-gray-300 shadow-sm focus:border-indigo-500 focus:ring-indigo-500 sm:text-sm"
          data-testid="fullName-input"
          disabled={isMutating}
          onBlur={handleBlur}
        />
        {errors.full_name && <p className="mt-1 text-sm text-red-600">{errors.full_name.message}</p>}
      </div>

      <div>
        <label htmlFor="date_of_birth" className="block text-sm font-medium text-gray-700">Date of Birth</label>
        <input
          id="date_of_birth"
          type="date"
          {...register('date_of_birth', { required: 'Date of Birth is required' })}
          className="mt-1 block w-full rounded-md border-gray-300 shadow-sm focus:border-indigo-500 focus:ring-indigo-500 sm:text-sm"
          data-testid="dateOfBirth-input"
          disabled={isMutating}
          onBlur={handleBlur}
        />
        {errors.date_of_birth && <p className="mt-1 text-sm text-red-600">{errors.date_of_birth.message}</p>}
      </div>

      <div>
        <label htmlFor="gender" className="block text-sm font-medium text-gray-700">Gender</label>
        <select
          id="gender"
          {...register('gender', { required: 'Gender is required' })}
          className="mt-1 block w-full rounded-md border-gray-300 shadow-sm focus:border-indigo-500 focus:ring-indigo-500 sm:text-sm"
          data-testid="gender-select"
          disabled={isMutating}
          onBlur={handleBlur}
        >
          <option value="">Select Gender</option>
          <option value="male">Male</option>
          <option value="female">Female</option>
          <option value="other">Other</option>
        </select>
        {errors.gender && <p className="mt-1 text-sm text-red-600">{errors.gender.message}</p>}
      </div>

      <div>
        <label htmlFor="phone_number" className="block text-sm font-medium text-gray-700">Phone Number</label>
        <input
          id="phone_number"
          type="tel"
          {...register('phone_number', { required: 'Phone Number is required' })}
          className="mt-1 block w-full rounded-md border-gray-300 shadow-sm focus:border-indigo-500 focus:ring-indigo-500 sm:text-sm"
          data-testid="phoneNumber-input"
          disabled={isMutating}
          onBlur={handleBlur}
        />
        {errors.phone_number && <p className="mt-1 text-sm text-red-600">{errors.phone_number.message}</p>}
      </div>

      <div>
        <label htmlFor="address" className="block text-sm font-medium text-gray-700">Address</label>
        <input
          id="address"
          type="text"
          {...register('address', { required: 'Address is required' })}
          className="mt-1 block w-full rounded-md border-gray-300 shadow-sm focus:border-indigo-500 focus:ring-indigo-500 sm:text-sm"
          data-testid="address-input"
          disabled={isMutating}
          onBlur={handleBlur}
        />
        {errors.address && <p className="mt-1 text-sm text-red-600">{errors.address.message}</p>}
      </div>

      {/* CV Content Field */}
      <div>
        <label htmlFor="cv_content" className="block text-sm font-medium text-gray-700">CV Content</label>
        {/* TODO: Integrate StatefulTextbox component (from Story 2.5) here */}
        <textarea
          id="cv_content"
          rows={10}
          {...register('cv_content', { required: 'CV Content is required' })}
          className="mt-1 block w-full rounded-md border-gray-300 shadow-sm focus:border-indigo-500 focus:ring-indigo-500 sm:text-sm"
          data-testid="cvContent-textarea"
          disabled={isMutating}
          onBlur={handleBlur}
        ></textarea>
        {errors.cv_content && <p className="mt-1 text-sm text-red-600">{errors.cv_content.message}</p>}
      </div>

      <button
        type="submit"
        className="inline-flex justify-center rounded-md border border-transparent bg-indigo-600 py-2 px-4 text-sm font-medium text-white shadow-sm hover:bg-indigo-700 focus:outline-none focus:ring-2 focus:ring-indigo-500 focus:ring-offset-2"
        data-testid="submit-button"
        disabled={!isDirty || isMutating || isUserCvLoading}
      >
        {isMutating ? 'Saving...' : 'Save Profile and CV'}
      </button>

      <BlockerDialog />
    </form>
  );
};

export default EditCVForm;
